<template>
  <div>
    <audio id="audio" ref="player" controls>
      <source :src="reuslt">
    </audio>
  </div>
</template>

<script>
export default {
  name: "AudioPlayer",
  props: {
    audioArrIndex: Number
  },
  data() {
    return {
      reuslt: "",
      srcArr: [
        require("../../assets/Songs/Aya Nakamura - Djadja.mp3"),
        require("../../assets/Songs/ALEX & VLADI - LOVE STORY.mp3"),
        require("../../assets/Songs/Ariana Grande - 7 rings.mp3"),
        require("../../assets/Songs/Ed Sheeran - Photograph.mp3"),
        require("../../assets/Songs/Lil John - Wat U gon do.mp3"),
        require("../../assets/Songs/Sia - Cheap Thrills.mp3"),
        require("../../assets/Songs/Papi Hans - Твой.mp3"),
        require("../../assets/Songs/Ava Max - Sweet but Psycho.mp3"),
      ]
    };
  },
  mounted() {
    this.reuslt = this.srcArr[0];
  },
  watch: {
    audioArrIndex: function() {
      this.reuslt = this.srcArr[this.audioArrIndex];
      this.$refs.player.load();
    }
  },
  created: function() {
    // Keyboar shortcuts for PC
    window.addEventListener("keydown", this.preventScrollingWithSpacebar);
    window.addEventListener("keyup", this.keyboardEvent);
  },
  methods: {
    keyboardEvent(e) {
      const keyCodes = [32, 37, 38, 39, 40, 77];
      if(!keyCodes.includes(e.keyCode))return;
      return {
        // playPause
        [keyCodes[0]]: () => (this.$refs.player.paused) ? this.$refs.player.play() : this.$refs.player.pause(),
        // moveBackward
        [keyCodes[1]]: () => (this.$refs.player.currentTime >= 5) ? this.$refs.player.currentTime -= 5 : '',
        // volumeUp
        [keyCodes[2]]: () => (this.$refs.player.volume < 1) ? this.$refs.player.volume += 0.1 : '',
        // volumeDown
        [keyCodes[3]]: () => (this.$refs.player.currentTime <= this.$refs.player.duration) ? this.$refs.player.currentTime += 5 : '',
        // mute
        [keyCodes[4]]: () => (this.$refs.player.volume > 0.01) ? this.$refs.player.volume -= 0.1 : '',
        // moveForward
        [keyCodes[5]]: () => (this.$refs.player.muted === true) ? this.$refs.player.muted = false : audio.muted = true,
      }[e.keyCode]();
    },
    preventScrollingWithSpacebar(e) {
      if(e.keyCode == 32 && e.target == document.body) e.preventDefault();
    }
  },
  destroyed: function() {
    window.removeEventListener("keydown", this.preventScrollingWithSpacebar);
    window.removeEventListener("keyup", this.keyboardEvent);
  },
};
</script>

<style lang="scss" scoped>
#audio {
  width: 100%;
  margin-bottom: 1rem;
}
</style>