<template>
  <div id="nav">
    <div
      v-for="(title, index) in songsTitleArr"
      @click="changeSong"
      :id="index"
      :class="index === currentElmentID ? 'navElement clicked' : 'navElement'"
      :key="index"
    >&nbsp;{{title}}&nbsp;</div>
  </div>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {
      currentElmentID: 0,
      songsTitleArr: [
        "1.Aya Nakamura - Djadja",
        "2.ALEX & VLADI - LOVE STORY",
        "3.Ariana Grande - 7 rings",
        "4.Ed Sheeran - Photograph",
        "5.Lil John - Wat U gon do",
        "6.Sia - Cheap Thrills",
        "7.Papi Hans - Твой",
        "8.Ava Max - Sweet but Psycho",
      ]
    };
  },
  methods: {
    changeSong(e) {
      this.currentElmentID = +e.target.id;
      this.$emit("audioArrIndex", +e.target.id);
    }
  }
};
</script>

