#nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 1rem;
  color: rgb(133, 130, 130);
}

.navElement {
  cursor: pointer;
  &:hover {
    background-color: #343109;
  }
  &:active {
    transform: scale(0.9);
  }
}

.clicked {
  color: #c0c0c0;
}
</style>